import dayjs from 'dayjs';
import { FilterType } from '../const';

const filter = {
  [FilterType.EVERYTHING]: (events) => [...events],
  [FilterType.FUTURE]: (events) => events.filter((event) => dayjs(event.dateFrom).isAfter(dayjs())), //   ,    Day.js    - .
  [FilterType.PAST]: (events) => events.filter((event) => dayjs(event.dateFrom).isSame(dayjs())), //  ,    Day.js    - .
  [FilterType.PRESENT]: (events) => events.filter((event) => dayjs(event.dateFrom).isBefore(dayjs())), //  ,    Day.js    - .
};

export {filter};

